import { HStack, Text } from '@chakra-ui/react'
import { useMemo } from 'react'
import Icon from './Icon'

export interface PaginationProps {
  total: number
  size: number
  currentPage: number
  onChange: (value: number) => void
  handleCurrentPage?: (value: number) => void
  value?: number
}

export default function Pagination(props: PaginationProps) {
  const { total, size, onChange, currentPage } = props
  const lastPage = useMemo(() => {
    return Math.ceil(total / size)
  }, [currentPage, total, size])

  const prev = () => {
    if (currentPage > 1) {
      onChange(currentPage - 1)
    }
  }
  const next = () => {
    if (currentPage < lastPage) {
      onChange(currentPage + 1)
    }
  }
  return (
    <HStack>
      <Icon
        iconCursor={currentPage == 1 ? 'not-allowed' : 'pointer'}
        xlinkHref="#icon-icon_left"
        svgFill={currentPage == 1 ? 'text_paragraph' : 'text_caption'}
        svgHover={currentPage == 1 ? 'text_paragraph' : 'primary'}
        onClick={currentPage == 1 ? () => {} : prev}
      />
      <Text color="text_caption" fontSize="14px" fontWeight="500">
        Page {currentPage} of {lastPage}
      </Text>
      <Icon
        iconCursor={currentPage == lastPage ? 'not-allowed' : 'pointer'}
        xlinkHref="#icon-icon_right"
        svgFill={currentPage == lastPage ? 'text_paragraph' : 'text_caption'}
        svgHover={currentPage == lastPage ? 'text_paragraph' : 'primary'}
        onClick={currentPage == lastPage ? () => {} : next}
      />
    </HStack>
  )
}
